.container {
  position: relative;
  height: 100vh;
  background-color: #fafbfd;
}

.cloud {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  max-width: 520px;
  width: 100%;
  text-align: center;
}

.bg {
  position: absolute;
  left: 0px;
  right: 0px;
  top: 50%;
  transform: translateY(-50%);
  z-index: -1;
}

.bg > div {
  width: 100%;
  background: #fff;
  border-radius: 90px;
  height: 125px;
}

.bg > div:nth-child(1) {
  box-shadow: 5px 5px 0px 0px #f3f3f3;
}

.bg > div:nth-child(2) {
  transform: scale(1.3);
  box-shadow: 5px 5px 0px 0px #f3f3f3;
  position: relative;
  z-index: 10;
}

.bg > div:nth-child(3) {
  box-shadow: 5px 5px 0px 0px #f3f3f3;
  position: relative;
  z-index: 90;
}

.cloud h1 {
  font-size: 8vw;
  text-transform: uppercase;
  font-weight: 700;
  margin-top: 0;
  margin-bottom: 1vw;
  color: #151515;
}

.cloud h2 {
  font-size: 2vw;
  margin: 0;
  font-weight: 700;
  color: #151515;
}

.cloud a {
  font-size: 1vw;
  text-decoration: none;
  text-transform: uppercase;
  background: #333;
  display: inline-block;
  padding: 15px 30px;
  border-radius: 5px;
  color: #fff;
  font-weight: 700;
  margin-top: 20px;
  margin: 5px;
}

.cloud a:hover {
  background-color: #555;
}

@media only screen and (max-width: 767px) {
  .bg {
    width: 270px;
    margin: auto;
  }

  .bg > div {
    height: 70px;
  }
}

@media only screen and (max-width: 480px) {
  .found h1 {
    font-size: 68px;
  }

  .found h2 {
    font-size: 18px;
  }
}
