.container {
  background-color: white;
  margin: 1vw;
  max-width: 1280px;
  overflow: hidden;
  background-color: rgb(34, 34, 34);
  color: #eee;
  font-family: "Noto Sans KR", sans-serif;
  border-radius: 0.4vw;
  display: flex;
  flex-direction: column;
  padding-top: 1vw;
}

.settingIcon {
  height: 100%;
  display: flex;
  align-items: flex-end;
  height: 7vw;
  padding-left: 1vw;
  padding-top: 1vw;
}

.setbar {
  display: flex;
  padding-left: 1vw;
  padding-right: 1vw;
}

input[type="number"] {
  -moz-appearance: textfield;
}

input {
  margin-left: 1vw;
  font-size: 2.5vw;
  height: 4.5vw;
  width: 10vw;
  background-color: rgb(20, 20, 20);
  border-radius: 0.4vw;
  color: white;
  font-weight: bold;
  outline: 0;
  border: 0;
  padding-left: 1vw;
  -webkit-appearance: none;
  flex: 1;
  margin: 1vw;
}
@media screen and (max-width: 899px) {
  input {
    font-size: 4.5vw;
    height: 6.5vw;
    width: 28vw;
  }
}
@media screen and (min-width: 1280px) {
  input {
    font-size: 40px;
    height: 50px;
    width: 50px;
  }
}
.setTitle {
  flex: 1;
  margin: 1vw;
  font-size: 3.5vw;
}

@media screen and (max-width: 899px) {
  .setTitle {
    flex: 1;
    margin: 1vw;
    font-size: 4.5vw;
  }
}

@media screen and (min-width: 1280px) {
  .setTitle {
    flex: 1;
    margin: 10px;
    font-size: 40px;
  }
}
.content {
  width: 100%;
}

.bar {
  background: #333;
  border-radius: 1vw;
  transition-duration: 200ms;
  max-height: 120vw;
  height: 100%;
  margin-bottom: 10px;
  overflow: hidden;
  box-shadow: 0 9px 16px 0 rgba(27, 31, 34, 0.35);
}

@media screen and (min-width: 1280px) {
  .bar {
    height: 700px;
  }
}
.bar_none {
  max-height: 0;
  transition-duration: 200ms;
  overflow: hidden;
}

.tabs {
  margin: 3vw;
  overflow: hidden;
}
@media screen and (min-width: 1280px) {
  .tabs {
    margin: 30px;
  }
}

.tabs span {
  margin: 0px;
  padding: 0px;
  font-size: 2vw;
  white-space: nowrap;
  display: inline-block;
}
@media screen and (min-width: 1280px) {
  .tabs span {
    margin: 0px;
    padding: 0px;
    font-size: 20px;
  }
}
.tabs li {
  display: inline-block;
  white-space: nowrap;
  font-size: 3.5vw;
  font-weight: bold;
  display: block;
  color: #fff;
  padding-left: 2vw;
  padding-top: 1vw;
  padding-bottom: 1.5vw;
  margin: 0.5vw;
  cursor: pointer;
  border-radius: 0.5vw;
  transition-duration: 100ms;
}
@media screen and (min-width: 1280px) {
  .tabs li {
    display: inline-block;
    white-space: nowrap;
    font-size: 35px;
    font-weight: bold;
    display: block;
    color: #fff;
    padding-left: 20px;
    padding-top: 10px;
    padding-bottom: 15px;
    margin: 5px;
    cursor: pointer;
    border-radius: 5px;
  }
}

.tabs li:hover {
  background-color: #222;
}
.tabs li:visited {
  background-color: #222;
}

.btn {
  width: 12vw;
  height: 12vw;
  cursor: pointer;
}

@media screen and (min-width: 1280px) {
  .btn {
    width: 140px;
    height: 140px;
    cursor: pointer;
  }
}

.timer {
  transition-duration: 200ms;
  font-size: 35vw;
  line-height: 35vw;
  text-align: center;
  overflow: hidden;
}
@media screen and (min-width: 1280px) {
  .timer {
    line-height: 450px;
    font-size: 450px;
  }
}

.status {
  transition-duration: 200ms;
  padding: 0vw 2vw 0vw 2vw;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  text-align: center;
}

@media screen and (min-width: 1280px) {
  .status {
    padding: 0vw 20px 0vw 20px;
  }
}
.status p {
  margin: 0px;
  padding: 0px;
  font-size: 4vw;
}
@media screen and (min-width: 1280px) {
  .status p {
    font-size: 35px;
  }
}

.status h3 {
  margin-top: 2vw;
  padding: 0px;
  font-size: 5vw;
}
@media screen and (min-width: 1280px) {
  .status h3 {
    margin-top: 20px;
    font-size: 50px;
  }
}

.metronomeBtn {
  flex: 1;
  width: 100%;
  text-align: center;
  border: none;
  padding: 1vw;
  margin-left: 4vw;
  margin-right: 1vw;
  margin-bottom: 2vw;
  border-radius: 15px;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.2);
  background-color: aliceblue;
  color: #696969;
  text-decoration: none;
  font-weight: 600;
  font-size: 5vw;
  transition: 0.25s;
  cursor: pointer;
}

/* .metronomeBtn:hover {
  color: #fff;
  background-color: rgb(37, 177, 60);
} */
.metronomeBtnCheck {
  color: #fff;
  background-color: rgb(37, 177, 60);
}

@media screen and (min-width: 1280px) {
  .metronomeBtn {
    font-size: 50px;
  }
}

.menu {
  font-size: 2.8vw;
  flex: 1;
  text-align: left;
  align-items: flex-start;
  cursor: pointer;
}

@media screen and (min-width: 1280px) {
  .menu {
    font-size: 30px;
  }
}

.line {
  background: #ffffff;
  margin-top: 1vw;
  margin-bottom: 1vw;
  width: 8vw;
  height: 1vw;
  border-radius: 2vw;
}

@media screen and (min-width: 1280px) {
  .line {
    margin-top: 10px;
    margin-bottom: 10px;
    width: 80px;
    height: 10px;
    border-radius: 20px;
  }
}

.boardContainer {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.boardList {
  flex: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  text-align: center;
}

.boardSet {
  font-size: 1.2vw;
  flex: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.boardCE {
  font-size: 1.2vw;
  flex: 1;
  transition-duration: 200ms;
  background-color: #333;
  justify-content: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.boardRS {
  font-size: 1.2vw;
  flex: 1;
  transition-duration: 200ms;
  background-color: #20cc30;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.boardFRS {
  font-size: 1.2vw;
  flex: 1;
  transition-duration: 200ms;
  background-color: #cc2030;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.boardFRS p {
  font-size: 3.5vw;
}

.setbarContainer {
  border: 2px solid #444;
  margin: 1vw;
  border-radius: 1vw;
}
