* {
  margin: 0;
  padding: 0;
}
.menuBar {
  padding: 1vw;
  background: #333;
  width: 100%;
}
.tabs {
  margin: 0;
}
.tabs li {
  font-size: 3vw;
  font-weight: bold;
  display: inline-block;
  color: #fff;
  padding: 1vw;
  cursor: pointer;
  transition-duration: 100ms;
}

.tabs li:hover {
  background-color: #222;
  /* color: #333; */
}
.tabs li:visited {
  background-color: #222;
  /* color: #333; */
}

@media screen and (max-width: 899px) {
  .menuBar {
    padding: 2vw;
    background: #333;
    width: 100%;
  }
  .tabs {
    margin: 0;
  }
  .tabs li {
    font-size: 5vw;
    font-weight: bold;
    display: inline-block;
    color: #fff;
    padding: 2vw;
    cursor: pointer;
    transition-duration: 100ms;
  }

  .tabs li:hover {
    background-color: #222;
    /* color: #333; */
  }
  .tabs li:visited {
    background-color: #222;
    /* color: #333; */
  }
}
